import calc from "postcss-calc";
import { useEffect, useState } from "react";

const NameList = ({ printTime }) => {
  const [id, setId] = useState(0);
  const [time, setTime] = useState({});
  const [flow, setFlow] = useState(false);
  const [times, setTimes] = useState([]);
  const [flowSec, setFlowSec] = useState("0");
  const start = (e) => {
    const start = new Date();
    setTime({ id: id, start: start });
    e.stopPropagation();
    setFlow(true);
    setInterval(calcTime, 1000);
    e.target.parentElement.querySelector(".timer").classList.remove("hidden");
  };

  const stop = (e) => {
    const end = new Date();
    setTime({ ...time, end: end });
    setFlow(false);
    setId(id + 1);
    clearInterval(calcTime);
    e.stopPropagation();
    e.target.parentElement.querySelector(".timer").classList.add("hidden");
  };

  const totalTime = (start, end) => {
    const remain = (end - start) / 1000;
    const s = Math.round(remain % 60);
    const m = Math.floor(remain / 60);
    return String(`${m < 10 ? "0" + m : m}:${s < 10 ? "0" + s : s}`);
  };

  useEffect(() => {
    if (time.end && flow === false) {
      setTimes((times) => [...times, time]);
    }
  }, [time, flow]);

  const calcTime = () => {
    console.log(new Date(), time);
  };

  // useEffect(() => {
  //   timerId.current = setInterval(() => {
  //     setMin(parseInt(time.current / 60));
  //     setSec(time.current % 60);
  //     time.current -= 1;
  //   }, 1000);

  //   return () => clearInterval(timerId.current);
  // }, []);
  // https://handhand.tistory.com/32

  const timeList = times.map((t) => (
    <li id={t.id} key={t.id}>
      start: {printTime(t.start)}, end: {printTime(t.end)}
      <br />
      during: {totalTime(t.start, t.end)}
    </li>
  ));

  return (
    <div className="hidden">
      <button onClick={start} disabled={flow ? true : false}>
        Start
      </button>
      <button onClick={stop} disabled={flow ? false : true}>
        Stop
      </button>
      <div
        className="hidden timer"
        style={{
          color: "red",
          textAlign: "center",
          fontSize: "20pt",
          fontWeight: "900",
        }}
      >
        {flow ? flowSec : null}
      </div>
      <ol>{timeList}</ol>
    </div>
  );
};

export default NameList;
