import { useState } from "react";
import "./iterationSample.css";
import NameList from "./NameList";

const IterationSample = () => {
  const [names, setNames] = useState([]);
  const [id, setId] = useState(1);
  const [inputText, setInputText] = useState("");

  const printTime = (time) => {
    const h = time.getHours();
    const m = time.getMinutes();
    const s = time.getSeconds();
    return String(
      `${h < 10 ? "0" + h : h}:${m < 10 ? "0" + m : m}:${s < 10 ? "0" + s : s}`
    );
  };

  const nameList = names.map((name) => (
    <li
      key={name.id}
      onDoubleClick={() => onRemove(name.id)}
      onClick={(e) => onClick(e)}
    >
      <strong>{name.text}</strong> <span>{`${printTime(name.time)}`}</span>
      <NameList printTime={printTime} />
    </li>
  ));

  const onChange = (e) => {
    setInputText(e.target.value);
  };
  const onSubmit = (e) => {
    e.preventDefault();
    if (inputText !== "") {
      const time = new Date();
      setNames(names.concat({ id: id, text: inputText, time: time }));
      setId(id + 1);
      setInputText("");
    }
  };
  const onKeyPress = (e) => {
    if (e.key === "Enter") {
      onSubmit(e);
    }
  };
  const onRemove = (id) => {
    setNames(names.filter((name) => name.id !== id));
  };

  const onClick = (e) => {
    e.target.closest("li").lastChild.classList.toggle("hidden");
  };

  return (
    <div>
      <h1>Time Logger</h1>
      <form onSubmit={onSubmit}>
        <input
          type="text"
          value={inputText}
          onChange={onChange}
          onKeyPress={onKeyPress}
        />
        <button>Add</button>
      </form>
      <ul>{nameList}</ul>
    </div>
  );
};

export default IterationSample;
